import React from 'react';
import HeroBackground from './heroBackground';
import HeroBody from './heroBody';
import HeroFooter from './heroFooter';
import HeroHeader from './heroHeader';

export {default as HeroBackground} from './heroBackground';
export {default as HeroBody} from './heroBody';
export {default as HeroFooter} from './heroFooter';
export {default as HeroHeader} from './heroHeader';
export interface IHeroProps {
  interior?: boolean;
  search?: boolean;
  accent?: boolean;
  light?: boolean;
  [others: string]: any;
}

const Hero: React.FC<IHeroProps> = ({className, interior, search, accent, light, children, ...rest}) => {
  const renderHero = () => {
    const _child = children;
    if (interior) {
      let _heroBackground: any = null;
      const _heroContents: any = [];

      React.Children.forEach(_child, (_childItem: any) => {
        if (_childItem)
          if (_childItem.type === HeroBackground) {
            _heroBackground = _childItem;
          } else if (_childItem.type === HeroBody || _childItem.type === HeroHeader || _childItem.type === HeroFooter) {
            _heroContents.push(_childItem);
          }
      });

      return (
        <div className={`hero_interior${accent ? '_accent' : ''}${light ? '_light' : ''} ${className || ''}`} {...rest}>
          <div className="hero_interior-left">{_heroContents}</div>
          {_heroBackground}
        </div>
      );
    } else if (search) {
      return (
        <div className={`hero_search${accent ? '_accent' : ''} ${className || ''}`} {...rest}>
          {_child}
        </div>
      );
    }
    return (
      <div className={`hero_content${accent ? '_accent' : ''} ${className || ''}`} {...rest}>
        {_child}
      </div>
    );
  };
  return renderHero();
};

export default Hero;
